<template>
  <div class="el-container">
    <!-- banner -->
    <v-Header :banner="false"/>
    <!--  -->
    <section v-lazy:background-image="`https://assets.vmei.com/system/cosmos/images/newCosmos/background1-mobile.png`" class="section-one">
      <div class="content">
        <p class="high-params">&nbsp;</p>
        <v-Hr class='hr' color="#fff"/>
        <p class="low-params"></p>
        <p class="low-params"></p>
      </div>
      <img class="output" src="https://assets.vmei.com/system/cosmos/images/newCosmos/output.png" alt="">
      <div class="form-container">
        <div class="title">
          
        </div>
        <form action="" v-on:submit.prevent="submitFunc">
          <span class="container-input">
            <input ref="companyname" maxlength="30" type="text" v-model="companyData.companyname" placeholder="">
            <input ref="companyAddress" maxlength="100" type="text" v-model="companyData.companyAddress" placeholder="">
          </span>
          <span class="container-input">
            <input ref="businessName" maxlength="30" type="text" v-model="contactsData.businessName" placeholder="">
            <input ref="businessEmail" maxlength="30" type="text" v-model="contactsData.businessEmail" placeholder="">
            <input ref="businessPhone" maxlength="11" type="text" v-model="contactsData.businessPhone" placeholder="">
            <input ref="wechat" maxlength="30" type="text" v-model="contactsData.wechat" placeholder="">
          </span>
          <button class="login">
            
            <!-- <span class="right"></span> -->
          </button>
        </form>
      </div>
    </section>

    <!--  -->
    <section class="section-two">
      <div class="list-container">
        <div class="zh-banner"></div>
        <v-Hr/>
        <span class="list">
          <img src="https://assets.vmei.com/system/cosmos/images/newCosmos/icon-pen.png" alt="logo">
          <span class="right">
            <span class="main">1.</span>
            <span class="info">15</span>
          </span>
        </span>
        <span class="list">
          <img src="https://assets.vmei.com/system/cosmos/images/newCosmos/icon-clock.png" alt="logo">
          <span class="right">
            <span class="main">2.</span>
            <span class="info">3-6</span>
          </span>
        </span>
        <span class="list">
          <img src="https://assets.vmei.com/system/cosmos/images/newCosmos/icon-email.png" alt="logo">
          <span class="right">
            <span class="main">3.</span>
            <span class="info">1</span>
          </span>
        </span>
        <span class="list">
          <img src="https://assets.vmei.com/system/cosmos/images/newCosmos/icon-shop.png" alt="logo">
          <span class="right">
            <span class="main">4.</span>
            <span class="info">3-5</span>
          </span>
        </span>
      </div>
      <div class="join-request">
        <h3></h3>
        <ul>
          <li>150</li>
          <li>2</li>
          <li>3R</li>
          <li>4</li>
          <li>5/</li>
          <li>6</li>
          <li>7</li>
        </ul>
        <router-link class="get-more" tag="button" to="/custmerJoinRequest"></router-link>
      </div>
    </section>

    <!--  -->
    <section class="section-three">
      <div class="zh-banner"></div>
      <v-Hr/>
      <img src="https://assets.vmei.com/system/cosmos/images/newCosmos/swiper-image.png" alt="logo">
    </section>
    <!--  -->
    <v-Footer></v-Footer>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  name: 'login',
  data () {
    return {
      loading: false,
      screenWidth: screen.availWidth,
      companyData: {
        companyname: '',
        companyAddress: ''
      },
      contactsData: {
        businessName: '',
        businessEmail: '',
        businessPhone: '',
        wechat: ''
      }
    }
  },
  mounted () {
    [
      ...document.querySelectorAll('input'),
      ...document.querySelectorAll('textarea')
    ].forEach(dom => {
      dom.addEventListener('blur', e => {
        const val = e.target.value;
        const reg = new RegExp(/('|")/g);
        if (val.match(reg)) {
          window.commonStart.tip(`"'`, 'warning');
          e.target.value = val.replace(reg, '');
        }
      })
    })
  },
  methods: {
    submitFunc (e) {
      if (this.companyData.companyname === '') {
        window.commonStart.tip('', 'warning');
        this.$refs.companyname.focus();
        return;
      } else if (this.companyData.companyAddress === '') {
        window.commonStart.tip('', 'warning');
        this.$refs.companyAddress.focus();
        return;
      } else if (this.contactsData.businessName === '') {
        window.commonStart.tip('', 'warning');
        this.$refs.businessName.focus();
        return;
      } else if (this.contactsData.businessEmail === '') {
        window.commonStart.tip('', 'warning');
        this.$refs.businessEmail.focus();
        return;
      } else if (this.contactsData.businessPhone === '') {
        window.commonStart.tip('', 'warning');
        this.$refs.businessPhone.focus();
        return;
      } else if (this.contactsData.wechat === '') {
        window.commonStart.tip('', 'warning');
        this.$refs.wechat.focus();
        return;
      };
      if (!this.contactsData.businessPhone.match(/1(2|3|4|5|6|7|8|9)\d\d{8}/)) {
        window.commonStart.tip('', 'warning');
        this.$refs.businessPhone.focus();
        this.$refs.businessPhone.value = '';
        return;
      } else if (!this.contactsData.businessEmail.match(/\w{3,}@\w+\.com/)) {
        window.commonStart.tip('', 'warning');
        this.$refs.businessEmail.value = '';
        this.$refs.businessEmail.focus();
        return;
      };
      axios({
        method: 'post',
        url: '/bussinessJoin/submit',
        params: {
          companyName: this.companyData.companyname,
          supplierType: 2,
          companyData: this.companyData,
          contactsData: this.contactsData
        }
      }).then(res => {
        this.loading = false;
        if (res.data.success) {
          /* eslint-disable */
          window.commonStart.tip('', 'success');
        } else {
          console.log(res.data)
          /* eslint-disable */
          window.commonStart.tip(res.data.msg, 'warning');
        }
      }).catch( err => {
        window.commonStart.tip(err, 'warning');
        console.log(err)
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.el-container {
  width: 100%;
  background-color: #fff;
  flex-direction: column;

  .section-one {
    color: #fff;
    background: #efefef;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: flex-start;
    background-size: contain;
    background-position: center 0%;
    background-repeat: no-repeat;
    padding-bottom: 20px;

    .content {
      color: #fff;
      text-align: center;
      margin-top: 95px;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: space-between;
      height: 120px;
      p.low-params {
        font-size: 14px;
        letter-spacing: 4px;
      }
      p.high-params {
        letter-spacing: 2px;
        font-size: 32px;
      }
      .hr {
        margin: 10px 0 15px;
      }
    }
    
    img.output {
      margin-top: 100px;
      z-index: 0;
      width: 100%;
    }

    .form-container {
      border-radius: 9px;
      z-index: 1;
      margin-top: -29vw;
      padding: 20px 20px 15px;
      padding-top: 0;
      width: 90%;
      flex: 0 0 auto;
      background-color: #fff;
      box-shadow: 1px 1px 100px 7px #00000036, 1px 13px 15px -13px #000000 inset;

      .title {
        height: 60px;
        font-weight: bold;
        width: 100%;
        font-size: 24px;
        display: inline-flex;
        align-items: center;
        justify-content: center;
        color: #000;
        img {
          margin-right: 20px;
        }
      }
      form {
        display: flex;
        flex-direction: column;
        color: rgb(103, 103, 103);

        .container-input {
          margin-bottom: 10px;
          border: 1px solid #e5e5e4;
          display: flex;
          flex-direction: column;
          align-items: center;
          justify-content: space-between;
          background: #fff;

          input + input {
            border-top: 1px solid #e5e5e4;
          }

          input {
            border: none;
            flex: 1;
            width: 100%;
            height: 100%;
            outline: none;
            padding: 14px;
            background-color: #fff !important;
          }
        }

        button {
          background: #ee4343;
          width: 100%;
          height: 45px;
          color: #fff;
          border: none;
          outline: none;
          text-align: center;
          padding: 0 20px;
          letter-spacing: 6px;
          cursor: pointer;

          .right {
            margin-left: 10px;
            font-size: 16px;
          }
        }
      }
    }
  }
  .section-two {
    background-color: #fff;
    margin: auto;
    display: flex;
    background-position: center;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    
    width: 100%;
    max-width: 1200px;
    padding: 30px 0;

    .list-container {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      align-items: center;
      padding: 0 0 25px;

      .zh-banner {
        font-size: 18px;
        font-weight: 600;
        letter-spacing: 4px;
      }

      .hr {
        margin: 10px 0;
      }

      .list {
        display: flex;
        flex-direction: row;
        justify-content: flex-start;
        align-items: center;
        width: 100%;
        padding: 0 20px;
        margin-top: 25px;
        img {
          width: 45px;
          margin: 13px;
        }
        .right {
          flex: 1 0 200px;
          display: inline-flex;
          flex-direction: column;
          justify-content: flex-start;
          .main {
            font-weight: 500;
            font-size: 16px;
            margin-bottom: 5px;
          }
          .info {
            color: #b3b3b3;
            font-size: 12px;
          }
        }
      }
    }
    .join-request {
      margin-top: 0px;
      line-height: 2em;
      width: 100%;
      background: #efefef;
      padding: 30px 20px;

      h3 {
        font-size: 14px;
        font-weight: bolder;
      }

      ul {
        list-style-type: none;
        font-size: 14px;
        line-height: 2.1em;
        margin-bottom: 20px;
        color: #6f6f6f;
      }
      button {
        margin: auto;
        display: block;
        padding: 10px 40px;
        color: #6f6f6f;
        background: inherit;
        border: 1px solid #6f6f6f;
        border-radius: 5px;
        cursor: pointer;
        outline: none;
      }
    }
  }
  .section-three {
    padding: 10px 0 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-end;
    text-align: center;
    position: relative;

    .zh-banner {
      font-size: 18px;
      letter-spacing: 0.3em;
      color: #333;
      display: inline-block;
    }
    .hr {
      margin: 10px 0;
    }
    img {
      width: 80%;
      margin-top: 60px;
    }
  }

}



</style>



